@keyframes spinAround {
  from {
    transform: rotate(0deg)
  }
  to {
    transform: rotate(359deg)
  }
}

@keyframes spinBounce {
  0% {
    transform: scale(0);
  }
  100% {
    transform: scale(1);
    opacity: 0;
  }
}

@keyframes translateX-0-200 {
  from {
    transform: translateX(0);
  }
  to {
    transform: translateX(200%);
  }
}
