.slider {
  display: flex;
  overflow: hidden;
  padding-bottom: 6px;

  &.-unanimated {
    .slider-view {
      transition: none;
    }
  }
}

.slider-view {
  position: relative;
  background-color: #fff;
  min-width: 100%;
  top: 0;
  left: 100%;
  float: left;
  transition: all .5s cubic-bezier(0.23, 1, 0.32, 1);
  max-height: 0;

  &.-active {
    transform: translateX(-100%);
    max-height: initial;
  }
}
