.card-list {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: $space-m 0;

  .card + .card {
    margin-top: $space-m;
  }
}

.card {
  position: relative;
  background-color: #fff;
  border: 1px solid $color-grey-200;
  border-radius: $border-radius-lg;
  box-shadow: 0 0 6px 1px rgba(0, 0, 0, .06);
  transition: box-shadow 0.3s ease;
  min-width: 100%;

  p {
    max-width: 532px;
    font-size: $size-text-m;
    font-weight: 300;
  }

  &:hover {
    box-shadow: 0 0 6px 1px rgba(0, 0, 0, .05),
                0 2px 16px 3px rgba(0, 0, 0, .05);
  }
}

.card-ribbon {
  position: absolute;
  border-radius: 0 4px 4px 0;
  top: $space-m;
  left: 0;
  height: 48px;
  width: 4px;
  background-color: $color-grey-300;

  &.-green {
    background-color: $color-green-600;
  }

  &.-red {
    background-color: $color-red-600;
  }

  &.-yellow {
    background-color: $color-yellow-600;
  }

  &.-sm {
    height: 36px;
  }
}

.card-row {
  padding: $space-m $space-m;
  border-bottom: 1px solid $color-grey-200;

  &.-borderless {
    border: none;
  }
}

.card-controls {
  padding: $space-s $space-m;
}
