.btn {
  &.-loading {
    color: transparent !important;
    text-shadow: none !important;
    position: relative;
    pointer-events: none;
    user-select: none;

    &:after {
      @include loader;
    }

    &[class*='btn-']:after {
      border-left-color: rgba(255, 255, 255, .75);
      border-bottom-color: rgba(255, 255, 255, .75);
    }
  }

  &.-borderless,
  // override Orquestra flat.css border selectors
  &.-borderless:not(.btn-primary):not(.btn-success):not(.btn-danger):not(.btn-info) {
    border: transparent;
  }

  &.-red {
    color: $color-red-600;
  }
}

.btn-secondary {
  background-color: #fff;

  &:hover {
    // override Orquestra flat.css box-shadow property
    box-shadow: inset 0 0 3px 1px rgba(0, 0, 0, 0.075) !important;
  }
}

.btn-action {
  // override Orquestra flat.css properties
  border: 1px solid transparent !important;
  color: $color-grey-600;
  background-color: transparent !important;
  background-image: none !important;
  text-shadow: none !important;
  box-shadow: none !important;

  &:hover,
  &:focus {
    color: $color-grey-900;
    text-decoration: none;
    background-color: $color-grey-100 !important;
  }

  &:active {
    box-shadow: inset 0 0 6px 1px rgba(0, 0, 0, 0.015);
  }
}

.btn-link {
  // override Orquestra flat.css border property
  border: transparent !important;
  color: $color-blue-600;

  &:hover,
  &:focus {
    text-decoration: none;
    opacity: 0.7;
  }
}

.btn-toolbar {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 36px;
  height: 36px;
  margin: 0;
  font-size: 14px;
  border-radius: 6px;
  cursor: pointer;

  .fas {
    color: $color-grey-500;
  }

  &:hover,
  &:focus {
    text-decoration: none;
    background-color: $color-grey-100;

    .fas {
      color: $color-grey-600;
    }
  }

  &:active {
    box-shadow: inset 0 0 6px 1px rgba(0, 0, 0, 0.015);
  }
}

.btn-icon {
  font-size: $size-text-xs;

  & + span {
    margin-left: 3px;
  }
}
