$drawer-width: 612px;

.drawer {
  display: flex;
  flex-direction: column;
  position: fixed;
  top: 0;
  right: 0;
  width: $drawer-width;
  height: 100%;
  background-color: #fff;
  border-left: 1px solid $color-grey-200;
  box-shadow: 0 0 18px rgba(0, 0, 0, 0.08);
  z-index: 1000;
  transition: all .5s cubic-bezier(0.23, 1, 0.32, 1);
  transform: translateX($drawer-width);

  &.-open {
    transform: translateX(0);
  }

  select,
  input[type=text] {
    width: 220px !important; // prevent Orquestra input default size override
  }
}

.drawer-body {
  display: block;
  position: relative;
  padding: $space-m;
  flex: 1;
  max-height: 100vh;
  overflow: auto;
}

.drawer-header {
  padding: 0 $space-m;
  border-bottom: 1px solid $color-grey-200;
}

.drawer-controls {
  border-top: 1px solid $color-grey-200;
  padding: $space-s $space-s;
}

.drawer-control {
  & + .drawer-control {
    margin-left: $space-xs;
  }
}

.drawer-btn-close {
  position: absolute;
  top: 12px;
  right: 12px;
}
