.empty {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: $space-m;

  &.-loading {
    position: relative;

    .empty-content,
    .empty-title {
      display: none;
    }


    &:after {
      content: 'Carregando...';
      position: absolute;
      display: block;
      left: 50%;
      transform: translateX(-50%);
      bottom: 0;
      font-size: $size-text-s;
      color: $color-link;
    }

    .empty-loader {
      position: relative;
      top: 100%;
      display: block;
      overflow: hidden;
      width: 164px;
      height: 4px;
      background-color: $color-grey-200;
      bottom: -$space-s;

      &:after {
        content: '';
        position: absolute;
        display: block;
        left: -100%;
        width: 100%;
        height: 100%;
        background-color: $color-link;
        animation: translateX-0-200 1s 0s ease-in-out infinite;
      }
    }
  }
}

.empty-icon {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100px;
  height: 100px;
  border-radius: 50%;
  background-color: #f9f9f9;
  font-size: 32px;
  color: $color-grey-500;
  box-shadow: inset 0 4px 12px 0 rgba(0, 0, 0, .05),
              inset 0 2px 3px 0 rgba(0, 0, 0, .025);
}

.empty-img {
  display: block;
  min-width: 320px;
}

.empty-content {
  text-align: center;
}

.empty-title {
  font-size: $size-title;
  font-weight: 700;
  color: $color-grey-700;
}

.empty-message {
  max-width: 374px;
  margin: 0 auto;
  font-size: $size-text-m;
  line-height: 1.5;
  color: $color-grey-500;
}

.empty-cta {
  margin-top: $space-s;
}
