$icon-size-base: 48px;
$icon-font-base: 24px;

$icon-size-sm: 36px;
$icon-font-sm: 18px;

.icon-badge {
  display: flex;
  justify-content: center;
  align-items: center;
  width: $icon-size-base;
  height: $icon-size-base;
  border-radius: 50%;
  background-color: $color-grey-100;
  color: $color-blue-600;
  font-size: $icon-font-base;

  // reset Orquestra bootstrap
  background-image: none;
  margin-top: 0;
  line-height: normal;

  &.-green {
    background-color: $color-green-100;
    color: $color-green-600;
  }

  &.-red {
    background-color: $color-red-100;
    color: $color-red-600;
  }

  &.-yellow {
    background-color: $color-yellow-100;
    color: $color-yellow-600;
  }

  &.-sm {
    width: $icon-size-sm;
    height: $icon-size-sm;
    font-size: $icon-font-sm;
  }

  &.-boxed {
    background-color: #fff;
    border-radius: 8px;
    box-shadow: 0 0 6px 1px rgba(0, 0, 0, .075);
  }
}
