$matrix-cell-size: 34px;
$matrix-inner-size: 24px;

.matrix {
  display: grid;
  grid-template-columns: repeat(36px);

  > * {
    min-width: $matrix-cell-size;
    min-height: $matrix-cell-size;
  }

  > [data-col='1'] {
    grid-column-start: 3;
  }

  > [data-col='2'] {
    grid-column-start: 4;
  }

  > [data-col='3'] {
    grid-column-start: 5;
  }

  > [data-row='1'] {
    grid-row: 3;
  }

  > [data-row='2'] {
    grid-row: 4;
  }

  > [data-row='3'] {
    grid-row: 5;
  }
}

.matrix-header {
  position: relative;
  display: flex;
  align-items: flex-end;
  color: $color-grey-700;
  font-weight: 600;

  grid-column-start: 3;
  grid-column-end: 6;

  > span {
    text-align: center;
    display: inline-block;
  }

  &.-vertical {
    grid-column-start: 1;
    grid-column-end: 2;
    grid-row: 3 / 6;

    > span {
      position: absolute;
      bottom: #{-$matrix-cell-size / 2 + 6px};
      left: 0;
      transform: rotate(-90deg);
      transform-origin: 0 0;
    }
  }
}

.matrix-label {
  line-height: $matrix-cell-size;
  text-align: center;
  color: $color-grey-500;
  font-weight: 300;
  font-size: $size-text-xs;

  &.-vertical {
    text-align: left;
  }
}

.matrix-cell {
  display: flex;
  justify-content: center;
  align-items: center;
}

.matrix-cell-inner {
  display: flex;
  justify-content: center;
  align-items: center;
  border: 1px solid $color-grey-300;
  width: $matrix-inner-size;
  height: $matrix-inner-size;
  background-color: #fff;
  border-radius: 4px;

  &:after {
    content: attr(data-value);
    font-weight: 600;
  }

  &.-active {
    box-shadow: 0 1px 6px 3px rgba(0, 0, 0, 0.06);
  }

  &[data-value='1'],
  &[data-value='2'] {
    background-color: $color-blue-100;
    color: $color-blue-600;
    border-color: $color-blue-200;
  }

  &[data-value='3'],
  &[data-value='4'] {
    background-color: $color-red-100;
    color: $color-red-600;
    border-color: $color-red-200;
  }

  &[data-value='6'] {
    background-color: $color-red-200;
    color: $color-red-700;
    border-color: $color-red-300;
  }

  &[data-value='9'] {
    background-color: $color-red-300;
    color: #fff;
    border-color: $color-red-400;
  }
}
