.secion-title {
  display: flex;
  flex-direction: column;

  h1 {
    color: $color-grey-900;
    font-size: $size-title-big;
    margin: 0;
  }
}

.secion-title-eyebrow {
  font-weight: 600;
  font-size: $size-text-m;
  color: $color-blue-700;
}

.secion-title-row {
  display: flex;
}

.secion-title-separator {
  height: 40px;
  display: block;
  width: 1px;
  background-color: #fff;
  box-shadow: 4px -8px 4px rgba(0, 0, 0, .25);
  margin: 8px 18px 0 10px;
  transform: rotate(25deg);
}
