#actions-drawer {
  width: 432px;
}

#section-actions.-loading {
  .card-toolbar {
    display: none;
  }

  .card-list-action {
    display: none;
  }
}

.risk-card {
  max-width: 900px
}

.action-title {
  margin-top: $space-m;
  margin-bottom: $space-l;

  h1 {
    margin-bottom: 0;
  }

  span {
    font-size: $size-text-base;
    color: $color-grey-600;
  }
}

.action-form {
  .form-group.-horizontal > .form-label {
    max-width: 160px;
    line-height: 30px;
  }
}
