$grid-gap: 16px;
$grid-gap-l: 24px;
$grid-gap-s: 12px;
$grid-gap-xs: 6px;

.columns {
  display: flex;
  flex-wrap: wrap;
  margin-left: -$grid-gap;
  margin-right: -$grid-gap;

  & + .columns {
    margin-top: $grid-gap;
  }

  &.-auto .column:not(.-grow) {
    flex: none;
  }

  &.-align-center {
    align-items: center;
  }

  &.-flex-end {
    justify-content: flex-end;
  }

  &.-gap-l {
    margin-left: -$grid-gap-l;
    margin-right: -$grid-gap-l;

    & + .columns {
      margin-top: $grid-gap-l;
    }

    .column:not(.-gapless) {
      padding-left: $grid-gap-l;
      padding-right: $grid-gap-l;
    }
  }

  &.-gap-s {
    margin-left: -$grid-gap-s;
    margin-right: -$grid-gap-s;

    & + .columns {
      margin-top: $grid-gap-s;
    }

    .column:not(.-gapless) {
      padding-left: $grid-gap-s;
      padding-right: $grid-gap-s;
    }
  }

  &.-gap-xs {
    margin-left: -$grid-gap-xs;
    margin-right: -$grid-gap-xs;

    & + .columns {
      margin-top: $grid-gap-xs;
    }

    .column:not(.-gapless) {
      padding-left: $grid-gap-xs;
      padding-right: $grid-gap-xs;
    }
  }
}

.column {
  flex: 1;
  max-width: 100%;
  padding-left: $grid-gap;
  padding-right: $grid-gap;

  &.-grow {
    flex: 1;
  }

  &.-align-end {
    align-self: flex-end;
  }

  &.-align-center {
    align-self: center;
  }

  &.-gapless {
    padding-left: 0;
    padding-right: 0;
  }
}
