// Orquestra Form Wrapper
#BoxFrmExecute {
  font-family: "Roboto", "Helvetica Neue", Helvetica, Arial, sans-serif;

  *, *:after, *:before {
    box-sizing: border-box;
  }

  a:not(.btn-link) {
    color: $color-link;
  }


  // prevent override Orquestra dropdown-menu color
  .dropdown-menu {

    a {
      color: #333;
    }

    a:hover,
    li.active a {
      color: #fff;
    }

  }
}

// Botão secundário
// #customBtn_Revisar
// #customBtn_RedefinirResponsavel
.btn#customBtn_Revisar,
.btn#customBtn_RedefinirResponsavel {
  border: 1px solid #ccc !important;
  text-shadow: none !important;
}

// Botão terciário - destrutivo
// #customBtn_cancelar
.btn#customBtn_cancelar {
  text-shadow: none !important;
  border: transparent !important;

  &:hover,
  &:focus {
    opacity: 0.7;
  }
}
